import React, { Component } from 'react'
import { Route, Switch } from 'react-router'
import { withRouter } from 'react-router-dom'
import DemoPage from '../pages/DemoPage'
import Tab1Page from '../pages/Tab1'
import Tab2Page from '../pages/Tab2'
import Tab3Page from '../pages/Tab3'
import Tab4Page from '../pages/Tab4'
import Layout from './hocs/Layout'
class Root extends Component {
  componentDidMount () {
    const { history, onRouteChange } = this.props
    history.listen((location, action) => {
      // location is an object like window.location
      onRouteChange(location, action)
    })
  }

  render () {
    // const { store } = this.props
    return (
      <Layout>
        <Switch>
            <Route key='' path='/' exact component={DemoPage} />
            <Route key='' path='/tab-1' exact component={Tab1Page} />
            <Route key='' path='/tab-2' exact component={Tab2Page} />
            <Route key='' path='/tab-3' exact component={Tab3Page} />
            <Route key='' path='/tab-4' exact component={Tab4Page} />
            <Route component={DemoPage} />
        </Switch>
      </Layout>
    )
  }
}


export default withRouter(Root)
