import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'

class LayoutFooter extends Component {
    constructor () {
        super()
        this.handleClickTab = this.handleClickTab.bind(this)
    }

    handleClickTab (e) {
        const { history } = this.props
        e.preventDefault()
        history.push(e.target.name)
    }

    render () {
        return (
            <ul id="layout-footer">
                <li><a onClick={this.handleClickTab} name='tab-1' href='#'>Tab 1</a></li>
                <li><a onClick={this.handleClickTab} name='tab-2' href='#'>Tab 2</a></li>
                <li><a onClick={this.handleClickTab} name='tab-3' href='#'>Tab 3</a></li>
                <li><a onClick={this.handleClickTab} name='tab-4' href='#'>Tab 4</a></li>
            </ul>
        )
    }
}

export default withRouter(LayoutFooter)